input,
button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: none;
  background-color: transparent;
  outline: none;
  color: inherit;
}
input {
  width: 100%;
}

button {
  cursor: pointer;
}
@media (hover: none) {
  a,
  button {
    cursor: none;
  }
}
